.list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-color: #d7dfe2;
  border-style: solid;
  border-width: 1px 0 0;
  padding: 10px 15px;
  gap: 40px;

  &__modal {
    display: flex;
    align-items: center;
    gap: 15px;
    border-color: #d7dfe2;
    border-style: solid;
    border-width: 1px 0 0;
    padding: 10px 15px;
  }

  &__container {
    display: flex;
    align-items: center;
    gap: 15px;
  }

  &__status__icon {
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #8bc34a;
  }

  &__text {
    color: #272743;
  }

  &__serialNumber {
    font-size: 10px;
    color: #8e9ba2;
  }

  &__guarantee {
    &__wrapper {
      display: flex;
    }

    &__text {
      width: 30px;
      align-self: flex-end;
      font-size: 12px;
      color: #8e9ba2;
    }
  }
}

.wrapper {
  display: grid;
  grid-template-rows: 1fr 1fr;
}